import wPicker from "@/components/w-picker/w-picker.vue"
import uniPopup from "@/components/uni-popup/uni-popup.vue"
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
import uniIcon from "@/components/uni-icon/uni-icon.vue"
export default {
	components:{
		wPicker,
		uniPopup,
		uniLoadMore,
		uniIcon
	},
	data(){
		return{
			shopObj:"",
			pageNo:1,
			pageAll:2,
			status:"loading",//加载状态
			enterType:2,//入驻状态1待审核2入住成功3审核失败
			allianceFunds:["8%","15%"],//联盟经费
			level:"",
			isFrontArr:["需要","不需要"],
			checkNote:"",//失败原因
			levelIndex:0,
			dataList:[],
			isShow:false,
			searchContent:"",
		}
	},
	methods:{
		map(){
			let that=this;
			uni.chooseLocation({
				success: function (res) {
					console.log('位置名称：' + res.name);
					console.log('详细地址：' + res.address);
					console.log('纬度：' + res.latitude);
					console.log('经度：' + res.longitude);
					that.$ajax({
						url:"/channelManagementRest/updateGeographicPosition",
						data:{
							latitude: res.latitude,
							longitude: res.longitude,
							shopId: that.shopObj.shopId
						},success(d){
							uni.showToast({
								title:"修改成功",
								icon:"none"
							})
						}
					})
				}
			})
		},
		//线下支付
		isFrontArrChange(e){
			console.log(e)
			let that=this;
			that.$ajax({
				url:"/channelManagementRest/updateLineIsFront",
				data:{
					marchantId:that.shopObj.merchantId,
					isFront:e.detail.value*1+1
				},
				success(d){
					that.dataList[0].isFront=d.isFront;
					that.shopObj.isFront=d.isFront;
					uni.setStorageSync('shopObj', JSON.stringify(that.shopObj))
				}
			})
		},
		//上面导航的切换
		typeChange(index){	
			this.enterType=index;
			this.init()
			this.search()
		},
		//重新提交
		sub(id){
			uni.navigateTo({
				url:"enter1?id="+id
			})
		},
		//数据初始代
		init(){
			let that=this;
			that.pageNo=1;
			that.pageAll=2;
			that.dataList=[];
			that.status="loading"
		},
		//查找
		searchName(){
			this.init();
			this.search();
		},
		//联盟经费的选择
		confirmFunds(e){
			let that=this;
			that.$ajax({
				url:"/channelManagementRest/updateLinePayRatio",
				data:{
					linePayRatio:e.detail.value==1?150:80,
					marchantId: that.shopObj.merchantId,
				},
				success(d){
					console.log(d)
					that.dataList[0].linePayRatio=d.linePayRatio
				}
			})
		},
		//级别的选择
		confirmLevel(e){
			this.levelIndex=e.detail.value;
		},
		statShow(msg){
			if(msg!=null){
				this.checkNote=msg
			}
			this.isShow=!this.isShow;
		},
		//查询
		search(){
			let that=this;
			console.log(that.shopObj)
			if(that.pageNo<=that.pageAll){
				console.log(5-that.levelIndex)
				that.$ajax({
					url:"/channelManagementRest/channelManagement",
					data:{
						ditchId:that.level[that.levelIndex].index,
						marchantId:that.shopObj.merchantId,
						pageNo:that.pageNo,
						name:that.searchContent,
						type:that.enterType
					},success(d){
						that.pageNo=that.pageNo*1+1;
						that.pageAll=d.totalPage;
						if(that.pageNo==2){
							that.dataList=d.lists||[];
						}else{
							let arr=that.dataList;
							that.dataList=that.dataList.concat(d.lists)
						}
						
						if(that.pageNo>that.pageAll){
							that.status="noMore"
						}
					}
				})
			}
		}
	},
	onReachBottom(){
		//上拉加载
		this.search()
	},
	onLoad(){
		var that=this;
		uni.getStorage({
			key:"shopObj",
			success(res){
				that.shopObj=JSON.parse(res.data);
				if(that.shopObj.level==1){
					that.level=[{
							text:"全部直属",
							index:""
						},{
							text:"B(互信版)",
							index:"3"
						},{
							text:"A2(互爱版)",
							index:"2"
						},{
							text:"A1(愉悦版)",
							index:"1"
						}]
				}else if(that.shopObj.level==2){
					that.level=[{
							text:"全部",
							index:""
						},{
							text:"A2(互爱版)",
							index:"2"
						},{
							text:"A1(愉悦版)",
							index:"1"
						}]
				}else if(that.shopObj.levelName="A1"){
					that.level=[{
						index:""
					}]
				}else{
					that.level=[{
						index:""
					}]
				}
			}
		})
		that.search()
	},
	onNavigationBarButtonTap(e){
		if(this.shopObj.level==3){
			uni.showToast({
				title:"您当前的级别不够，请去升级",
				icon:"none"
			})
			return 
		}
		uni.navigateTo({
			url:"enter1"
		})
	}
}
